import cart from '../../api/cart'

const state = {}

const getters = {}

const mutations = {}

const actions = {
  addCart ({ commit }, itemData) {
    cart.addToCart(payload => {
      commit('setcart', payload)
      window.location.href = '/checkout'
    }, itemData)
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
